
.main-footer {
  background-color: rgba(0, 28, 50, 1);
  padding-top: 90px;
}


.advice {
  width: 340px;
  font-size: 22px;
  color: $mainColor;
  word-spacing: 3px;
}

.company-info {
  font-weight: lighter;
  color: $descColor;
  width: 240px;
  line-height: 1.5;
}

.company-info__adress, .company-info__email, .company-info__Regon {
  margin-bottom: 20px;
}
.about-us {
  color: $mainColor;
  font-weight: lighter;
  font-size: 14px;
  line-height: 1.5;
}
.about-us__desc {
  height: 170px;
  overflow: hidden;
  margin-bottom: 35px;
}
.about-us__person {
  font-size: 20px;
  font-weight: bolder;
}

.about-us__position {
  color: $descColor;
}

.about-us__link {
  text-decoration: none;
  color: #9ebcd4;
  font-weight: lighter;
}
.column--grow-2  {
  margin-right: 30px;
  @media (max-width: 930px) {
    margin-bottom: 70px;
    margin-right: 0;
  }
}

.column--grow-3 {
  margin-left: 80px;
  
  @media (max-width: 930px) {
    margin-top: 70px;
    margin-left: 0;
  }
}

.row--footer {
  margin-bottom: 100px;

  @media (max-width: 930px) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 50px;
  
  @media (max-width: 930px) {
    flex-direction: column;
  }
}

.logo--footer {
  color: $mainColor;
}

.scroll-top {
  width: 60px;
  height: 60px;
  background-color: #f82e46;
  border-radius: 50%;
  position: relative;
  
  &:after {
    content: url(../images/aa.png);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  @media (max-width: 930px) {
    margin-bottom: 50px;
    margin-top: 50px;
  }
}
.rights {
  font-size: 14px;
  font-weight: lighter;
  color: $descColor;
}

.red-bottom {
  height: 11px;
  background-color: #f82e46;
}