
.clients-rewievs {
  margin-top: 110px;
  background-color: rgba(0, 28, 50, 1);
}

.header--reviews {
  padding-top: 80px;
  padding-bottom: 60px;
}

.header__title--white {
  color: $mainColor;
}
.row--client {
  padding-bottom: 100px;
  
  @media (max-width: 776px) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
.column--client {
  display: flex;
  flex-direction: column;
  max-width: 270px;
  align-items: center;
  position: relative;
  
  @media (max-width: 776px) {
    margin-bottom: 100px;
  }
}

.client__photo {
  height: 110px;
  width: 110px;
  overflow: hidden;
  border-radius: 50%;
  margin-bottom: 30px;
  position: relative;
  z-index: 1;
  
  &:first-child {
     top: 0;
    left: 0;
  }
}
.client__img {
  position: absolute;
  top: -10%;
  left: -50%
}

.pos {
  left: -15px;
  top: 0;
}


.photo__icon {
  position: absolute;
    right: 22%;
    top: 15%;
    height: 38px;
    width: 57px;
    background-image: url(../images/Bez-nazwy-31.png);
    background-position: center center;
    z-index: 10;
}
.client__review {
  font-family: 'Playfair Display', serif;
  font-size: 17px;
  color: #acd0ec;
  text-align: center;
  height: 245px;
  overflow: hidden;
  line-height: 1.6;
  margin-bottom: 60px;
}

.client__name {
  color:$mainColor;
  margin-bottom: 10px;
  font-weight: bolder;
  position: relative;
  
  &:after {
    content: '';
    position: absolute;
    top: -30px;
    left: 50%;
    width: 15px;
    height: 3px;
    background-color: red;
    transform: translateX(-50%);
  }
}

.client_company {
  font-weight: lighter;
  color: $descColor;
}
