.header {
  text-align: center;
}

.header__title {
  font-weight: bold;
  font-size: 30px;
  color: $titleColor;
  text-transform: uppercase;
  margin-bottom: 35px;
}

.header_desc {
  color: $subTitleColor;
  margin-bottom: 70px;
  word-spacing: 5px;
  max-width: 830px;
  font-size: 22px;
  font-weight: lighter;
  margin: 0 auto;
  margin-bottom: 70px;
}


.header__title--align-left {
  text-align: left;
  color: $subTitleColor;
  margin-bottom: 65px;
}