
.main-questions {
  margin-top: 80px;
}


.questions {
  margin-bottom: 120px;
}
.questions__list {
  margin-bottom: 60px;
    padding-left: 45px;
}
.question__text {
  font-size: 20px;
  font-weight: lighter;
  color: #99a2a9;
  margin-bottom: 20px;
  position: relative;

  
  &:after {
    content: '';
    position: absolute;
    left: -45px;
    top: 30%;
    width: 20px;
    height: 10px;
    background-image: url(../images/Bez-nazwy-8.png);
    background-position: center center;
    background-repeat: no-repeat;
  }
}

.questions__answer {
  font-weight: lighter;
  color: #8d979e;
}