
.main-benefits {
  margin-top: 80px;
}

.profits {
  @media (max-width: 820px) {
    flex-wrap: wrap;
  }
  @media (max-width: 599px) {
    justify-content: center;
  }
  @media (max-width: 463px) {
    flex-direction: column;
    justify-content: center;
  }
}
.profits__element {
  display: flex;
  flex-direction: column;
  align-items: center;
  @media (max-width: 820px) {
    margin: 20px;
  }
  
}

.profits__icon {
  margin-bottom: 20px;
}

.profits__title {
  color: $titleColor;
  font-size: 22px;
  text-align: center;
  margin-bottom: 20px;
}

.profits__desc {
  font-weight: lighter;
  font-size: 20px;
  max-width: 220px;
  word-break: auto;
  text-align: center;
}