
.main-section {
  height: 100vh;
  
  @media (max-width: 780px) {
    height: unset;
  }
}
.main-header {
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  padding: 20px;
  
  @media (max-width: 732px) {
    flex-direction: column;
  }
}

.contact {
  display: flex;
  align-content: center;
}
.contact__wrapper {
  margin-left: 30px;
  display: flex;
  align-items: center;
  
  @media (max-width: 520px) {
    flex-direction: column;
  }
}
.contact__tel {
  font-weight: lighter;
  text-decoration: none;
  color: #405565;
}
.contact__mail {
  font-weight: lighter;
  text-decoration: none;
  color: #405565;
  word-break: break-all;
}
.contact__tel-icon {
  margin-right: 5px;
}


.main-section__wrapper {
  background-image: linear-gradient(rgba(0, 28, 50, 0.7), rgba(0, 28, 50, 0.7)),url(../images/Bez-nazwy-1.jpg);
  background-position: center;
}
//main-banner
.main-banner {
  text-align: center;
  margin-top: 80px;
}

.title {
  font-size: 40px;
  font-weight: bold;
  color: $mainColor;
  text-transform: uppercase;
  margin-bottom: 20px;
  
  @media (max-width: 520px) {
    font-size: 25px;
  }
}

.title__desc {
  color: $descColor;
  font-weight: lighter;
  font-size: 24px;
  margin-bottom: 30px;
  @media (max-width: 520px) {
    font-size: 20px;
  }
}

.contact-link {
  display: inline-block;
  text-decoration: none;
  padding: 8px 80px;
  background-color: #f82e46;
  text-transform: uppercase;
  margin-bottom: 10px;
  border-radius: 30px;
  color: $mainColor;
}

.link-desc {
  font-weight: lighter;
  font-size: 14px;
  color: $descColor;
}

.company-services {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
  text-align: center;
}

.company-services__icon {
  margin-bottom: 15px;
  height: 63px;
}
.company-services__img {}
.company-services__desc {
  max-width: 255px;
  color: $mainColor;
  word-spacing: 2px;
  line-height: 1.4;
}

.company-services__title {
  font-size: 20px;
  color: $mainColor;
  margin-bottom: 10px;
  word-spacing: 4px;
}

.company {
  @media (max-width: 778px) {
    flex-direction: column;
    padding-bottom: 50px;
  }
}

.company_item {}