@import './_reset.scss';

@import url('https://fonts.googleapis.com/css?family=Montserrat:300,400,700');
@import url('https://fonts.googleapis.com/css?family=Playfair+Display:400i');

$descColor: #dfe3e5;
$mainColor: #fff;
$titleColor : #001c32;
$subTitleColor: #a5adb3;


* {
  box-sizing: border-box;
}

body {
  font-family: Montserrat;
  font-size: 16px;
}

.container {
  margin: 0 auto;
  max-width: 1250px;
  
  @media (max-width: 1000px) {
    max-width: 890px;
  }
  
  @media (max-width: 800px) {
    max-width: 680px;
  }

  @media (max-width: 600px) {
    max-width: 500px;
  }
  @media (max-width: 500px) {
    max-width: 400px;
  }
  @media (max-width: 400px) {
    max-width: 300px;
  }
}


.row {
  display: flex;
  justify-content: space-between;
}

.column {}

@import './header';
@import './logo';
@import './main-benefits';
@import './client-reviews';
@import './main-footer';
@import './main-questions';
@import './main-section';